import React from 'react';
// import ReactDOM from 'react-dom';
// import Select from 'react-select'
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';


class SelectorPanel extends React.Component {
    constructor(props) {
        super(props);



        this.state = {
            showRoutes: [],
            showStops: [],

            selectedRoute: null,
            selectedStop: null,
            t:"init"
        }
    }
    componentDidMount() {
        let tmpShowRoutes = JSON.parse(JSON.stringify(this.props.routes));
        this.setState({ showRoutes: tmpShowRoutes });
    }
    render() {
        const handleRouteChange = (route) => {
            if(!route) {
                return;
            }
            let tmpShowStops = [];

            let matchMappings = this.props.mappings.filter((mapping) =>
                mapping.route === route.route && mapping.bound === route.bound
            );

            matchMappings.forEach((mapping) => {
                let matchStop = this.props.stops.filter((stop) =>
                    mapping.stop === stop.stop
                )[0];
                tmpShowStops.push(matchStop);
            })

            this.setState({
                showStops: tmpShowStops,
                stopValue: null,
                selectedStop: null,
                selectedRoute: route,
            });
        }
        const handleStopChange = (stop) => {
            if(!stop) {
                return;
            }
            this.setState({
                selectedStop: stop
            })
        }
        const handleAddTrack = () => {
            this.props.addTrack(this.state.selectedRoute, this.state.selectedStop);
            this.setState({
                selectedRoute: null,
                selectedStop: null,
            });
        }
        return (
            <div>
                <p>08</p>
                {
                    this.state.selectedRoute
                    ?<p>{this.state.selectedRoute.route}</p>
                    :<p>null</p>
                }
                {
                    this.state.selectedStop
                    ?<p>{this.state.selectedStop.name_tc}</p>
                    :<p>null</p>
                }
                
                  <h1>{this.state.t}</h1>
                
                <Autocomplete
                    clearOnBlur={false}
                    options={this.state.showRoutes}
                    getOptionLabel={(option) => (option.orig_tc + " -> " + option.dest_tc)}
                    renderInput={(params) => <TextField {...params} autocomplete={false} label="Route" variant="outlined" />}
                    onChange={(e, newValue) => { handleRouteChange(newValue);this.setState({t:"dsa"}) }}
                    value={this.state.selectedRoute}
                    disableClearable={true}
                    blurOnSelect={true}
                ></Autocomplete>
                <Autocomplete
                    options={this.state.showStops}
                    getOptionLabel={(option) => (option.name_tc)}
                    renderInput={(params) => <TextField {...params} label="Stop" variant="outlined" />}
                    onChange={(e, newValue) => { handleStopChange(newValue) }}
                    value={this.state.selectedStop}
                ></Autocomplete>
                <Button disabled={this.state.selectedRoute == null || this.state.selectedStop == null} style={{ width: '100%' }} variant="outlined" onClick={(e) => { handleAddTrack(e) }}>Add</Button>
            </div>
        )
    }
}


export default SelectorPanel;